:root {
  --Light-grayish-cyan-1: hsl(185, 41%, 84%);
  --Light-grayish-cyan-2: hsl(189, 41%, 97%);
  --White: hsl(0, 0%, 100%);
  --Dark-grayish-cyan-1: hsl(184, 14%, 56%);
  --Dark-grayish-cyan-2: hsl(186, 14%, 43%);
  --Very-dark-cyan: hsl(183, 100%, 15%);
  --Strong-cyan: hsl(172, 67%, 45%);
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder {
  color: transparent;
}

input:focus::placeholder {
  color: transparent;
}

.Title {
  color: hsl(183, 100%, 15%);
  color: var(--Very-dark-cyan);
  padding-bottom: 10px;
}

.App {
  background-color: hsl(185, 41%, 84%);
  background-color: var(--Light-grayish-cyan-1);
  font-family: 'Space Mono', monospace;
}

.Card {
  display: flex;
  width: 62.5rem;
  height: 31.25rem;
  border-radius: 40px;
  background-color: hsl(0, 0%, 100%);
  background-color: var(--White);
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
}

.Bill {
  padding: 51px 50px 20px;
}

.Bill-Text {
  padding-bottom: 11px;
  color: hsl(183, 100%, 15%);
  color: var(--Very-dark-cyan);
}

.Bill-Input {
  background-color: hsl(189, 41%, 97%);
  background-color: var(--Light-grayish-cyan-2);
  text-align: right;
  border-radius: 10px;
  /* padding-right: 10px; */
}

.Bill-Input-Field {
  display: flex;
  width: 379px;
  height: 48px;
  background-color: hsl(189, 41%, 97%);
  background-color: var(--Light-grayish-cyan-2);
  justify-content: space-around;
  align-items: center;
  border-radius: 5px;
}

.Bill-Input-Field:hover {
  border: solid hsl(172, 67%, 45%);
  border: solid var(--Strong-cyan);
  border-width: 0.15em;
}

.Person-Icon {
  width: 14px;
  height: 16px;
  vertical-align: middle;
}

.Tip {
  padding: 0px 50px 20px;
}

.Tip-Grid {
  display: flex;
  width: 379px;
  height: 111px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

button {
  height: 48px;
  width: 117px;
  border-radius: 5px;
  background-color: hsl(183, 100%, 15%);
  background-color: var(--Very-dark-cyan);
  color: hsl(0, 0%, 100%);
  color: var(--White);
}

.Custom-Tip {
  height: 48px;
  width: 117px;
  border-radius: 5px;
  background-color: hsl(189, 41%, 97%);
  background-color: var(--Light-grayish-cyan-2);
  text-align: center;
}

.Custom-Tip:focus {
  text-align: right;
  padding-right: 10px;
  border: solid hsl(172, 67%, 45%);
  border: solid var(--Strong-cyan);
  border-width: 0.15em;
}

button:focus {
  background-color: hsl(172, 67%, 45%);
  background-color: var(--Strong-cyan);
}

.Amount {
  width: 413px;
  height: 417px;
  background-color: hsl(183, 100%, 15%);
  background-color: var(--Very-dark-cyan);
  border-radius: 40px;
  display: grid;
  flex-direction: column;
  grid-template-columns: 103px 103px 103px 103px;
  grid-template-rows: 69.5px 69.5px 69.5px 69.5px 69.5px 69.5px;
  grid-template-areas:
            "th3 th3 th1 th1"
            "th4 th4 th1 th1"
            "toth3 toth3 toth1 toth1"
            "toth4 toth4 toth1 toth1"
            ". R R .";
}

.Amount-Reset {
  grid-area: R;
  background-color: hsl(184, 14%, 56%);
  background-color: var(--Dark-grayish-cyan-1);
  width: 206px;
}

.Tip-h3 {
  width: 208px;
  grid-area: th3;
  text-align: start;
  padding-top: 46px;
  color: hsl(0, 0%, 100%);
  color: var(--White);
  font-size: 18px;
  padding-left: 25%;
}

.Tip-h1 {
  height: 48px;
  width: 208px;
  float: right;
  grid-area: th1;
  text-align: center;
  padding-top: 38px;
  font-size: 35px;
  color: hsl(172, 67%, 45%);
  color: var(--Strong-cyan);
}

.Tip-h4 {
  width: 208px;
  grid-area: th4;
  text-align: center;
  font-size: 12px;
}

.Total-h1 {
  height: 48px;
  width: 208px;
  float: right;
  grid-area: toth1;
  text-align: center;
  padding-top: 38px;
  font-size: 35px;
  color: hsl(172, 67%, 45%);
  color: var(--Strong-cyan);
}

.Total-h3 {
  width: 208px;
  grid-area: toth3;
  text-align: start;
  padding-top: 46px;
  color: hsl(0, 0%, 100%);
  color: var(--White);
  font-size: 18px;
  padding-left: 25%;
}

.Total-h4 {
  width: 208px;
  grid-area: toth4;
  text-align: center;
  font-size: 12px;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
  -moz-appearance: textfield; /* Firefox */
}

